:root {
  --main-bcg: #84a278;
  --modal-bcg: #fff;
  --cream-bcg: #aec6a5;
  --coffee-bcg: #efd478;
  --milk-bcg: #d6936d;
  --cream-img-bcg: linear-gradient(0deg, rgba(156, 190, 143, .7), rgba(156, 190, 143, .7)), #d9d9d9;
  --coffee-img-bcg: linear-gradient(0deg, rgba(248, 210, 78, .7), rgba(248, 210, 78, .7)), #d9d9d9;
  --milk-img-bcg: linear-gradient(0deg, rgba(213, 117, 63, .7), rgba(213, 117, 63, .7)), #d9d9d9;
  --cow-bcg: linear-gradient(0deg, rgba(248, 210, 78, .7), rgba(248, 210, 78, .7)), #d9d9d9;
  --first-font-color: #131313;
  --second-font-color: #000;
  --alt-font-color: #fcfcfc;
  --time-font-color: #907e82;
  --contacts-font-color: #d6936d;
  --bottom-font-color: #7a7a7a;
}

@media screen and (min-width: 320px) {
  html, body {
    overflow-x: hiden;
  }
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

body {
  background-color: var(--modal-bcg);
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

link {
  text-decoration: none;
}

button {
  background: none;
  border: none;
  padding: 0;
  line-height: 0;
}

.visually-hiden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 767px) {
  .container {
    max-width: 480px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.hero {
  text-align: center;
  height: 415px;
  background-color: var(--main-bcg);
  color: var(--alt-font-color);
}

.about-section {
  background: #84a178;
}

.about-section__caption {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: #fcfcfc;
  padding-top: 60px;
  font-family: Montserrat;
  font-size: 30px;
  font-style: normal;
  font-weight: 800;
  line-height: 1.23;
}

.about-section__title {
  text-align: center;
  letter-spacing: .06em;
  text-transform: uppercase;
  color: #fcfcfc;
  margin-top: 12px;
  font-family: Seymour One;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.25;
}

